






















/* my css */
* {
  margin: 0;
  padding: 0;
  font-family: 'Nunito', sans-serif;
  color: #636b6f;
}
html {
  height: 100vh;
  width: 100%;
}
.page {
  margin: 0 22vw;
}
/* end my css */
/* header's css */
header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 10vh;
}
nav {
  padding: 20px 0;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
ul {
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
li {
  padding: 0 3vw;
}
a.nav-link {
  text-decoration: none;
  font-size: 1.2em;
}
a.nav-link:hover {
  color: #c5c5c5;
}
div.page-title {
  font-size: 64px;
  text-align: center;
}
/* end header's css */
/* responsive */
@media screen and (max-width: 499px) { 
  /*　画面サイズ 499pxまで適用　*/
html {
    font-size: 80%;
}
.page {
    margin: 0 4vw;
}
div.page-title {
    font-size: 44px;
}
}
@media screen and (min-width: 500px) and (max-width: 604px) {
  /*　画面サイズ 500pxから604pxまで適用　*/
html {
    font-size: 88%;
}
.page {
    margin: 0 4vw;
}
}
@media screen and (min-width: 605px) and (max-width: 699px) {
  /*　画面サイズ 605pxから699pxまで適用　*/
.page {
    margin: 0 8vw;
}
}
@media screen and (min-width: 700px) and (max-width: 999px) {
  /*　画面サイズ 700pxから999pxまで適用　*/
.page {
    margin: 0 12vw;
}
}
/* end responsive */

















/* welcome's css */
main {
  position: relative;
  height: 90vh;
  width: 100%;
}
.top {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateY(-50%) translateX(-50%);
          transform: translateY(-50%) translateX(-50%);
  /* -webkit- transform: translateY(-50%) translateX(-50%); */
}
.top .title {
  font-size: 84px;
  margin-bottom: 30px;
}
.top p {
  font-size: 20px;
}
/* end welcome's css */
/* responsive */
@media screen and (max-width: 499px) { 
  /*　画面サイズ 499pxまで適用　*/
.top .title {
    font-size: 50px;
    margin-bottom: 20px;
}
.top p {
    font-size: 16px;
}
}
/* responsive */


































































