/* links' css */
.links-text {
  padding-top: 40px;
  text-align: center;
}
.links-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
div.link-name {
  padding-top: 40px;
  text-align: center;
}
.links-img img {
  width: 80px;
  height: 80px;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  margin: 20px 80px 0;
  border-radius: 5px;
  border: solid 1px rgba(200, 200, 200, 0.5);
}
.links-img img:hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
}
/* end links' css */
/* responsive */
@media screen and (max-width: 499px) { 
  /*　画面サイズ 499pxまで適用　*/
.links-img img {
    width: 50px;
    height: 50px;
    margin: 16px 16px 0;
}
}
@media screen and (min-width: 500px) and (max-width: 604px) {
  /*　画面サイズ 500pxから604pxまで適用　*/
.links-img img {
    margin: 20px 40px 0;
}
}
/* end responsive */
